.container {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #b1b1b1;
    overflow: hidden;
    margin: 15px 0px;

    .btn {
        width: 45px;
        height: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 19px;
        background-color: #313131;
        transition: background 500ms ease;
        user-select: none;
        cursor: pointer;

        &:hover {
            background-color: #1e1e1e;
        }
    }

    .input {
        width: calc(100% - 45px);
        height: 40px;
        border: none;
        font-size: 16px;
        padding: 10px;
        color: #cfcece;
        outline: none;
        background-color: #3e4446;
    }
}